* {
  box-sizing: border-box;
}

p {
  margin: 0;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  width: 100%;
  height: auto;
  margin: 0;
  display: block;
}

.search-form {
  width: 100%;
  background-color: #00f;
  justify-content: center;
  margin: 0;
  display: flex;
  position: fixed;
}

input {
  width: 300px;
  height: 30px;
  border: #0000;
  border-top-left-radius: 2px;
  border-bottom-left-radius: 2px;
  outline: none;
  margin-top: 10px;
  margin-bottom: 10px;
  padding-left: 10px;
  padding-right: 10px;
}

.form-btn {
  cursor: pointer;
  height: 30px;
  z-index: 99999;
  border: #0000;
  border-top-right-radius: 2px;
  border-bottom-right-radius: 2px;
  margin-top: 10px;
  margin-bottom: 10px;
}

.form-btn:hover, .form-btn:focus {
  background-color: #ff0;
}

.gallery {
  max-width: 1400px;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  margin-left: auto;
  margin-right: auto;
  padding-top: 70px;
  padding-bottom: 30px;
  display: flex;
}

.photo-card {
  box-shadow: 0 1px 3px #0000001a, 0 1px 2px #0000000f;
}

.photo-card img {
  width: 320px;
  height: 320px;
  object-fit: cover;
}

.info {
  width: 320px;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0;
  padding: 10px;
  display: flex;
}

.info-item {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.load-more {
  width: 100px;
  height: 100px;
  cursor: pointer;
  background-color: #0ff;
  border-color: #0000;
  border-radius: 50%;
  margin-left: auto;
  margin-right: auto;
  display: block;
  box-shadow: 0 54px 55px #00000040, 0 -12px 30px #0000001f, 0 4px 6px #0000001f, 0 12px 13px #0000002b, 0 -3px 5px #00000017;
}

